import { getProductBySlug } from "@/lib/products";
import { notFound } from "next/navigation";
import Image from "next/image";
import styles from "./product.module.css";

export default async function ProductPage({ params }: { params: Promise<{ slug: string }> }) {
    const { slug } = await params;
    const product = getProductBySlug(slug);

    if (!product) {
        notFound();
    }

    // Schema Markup
    const jsonLd = {
        "@context": "https://schema.org/",
        "@type": "Product",
        "name": product.title,
        "image": [product.image],
        "description": product.reviewSummary,
        "brand": {
            "@type": "Brand",
            "name": product.category
        },
        "offers": {
            "@type": "Offer",
            "url": product.link,
            "priceCurrency": "USD",
            "price": product.price,
            "availability": "https://schema.org/InStock"
        },
        "aggregateRating": {
            "@type": "AggregateRating",
            "ratingValue": product.rating,
            "reviewCount": "128"
        }
    };

    return (
        <div className={styles.productPage}>
            <script
                type="application/ld+json"
                dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
            />

            {/* Sticky Buy Bar */}
            <div className={styles.stickyBar}>
                <div className="container">
                    <div className={styles.stickyInner}>
                        <div className={styles.stickyInfo}>
                            <img src={product.image} alt="" className={styles.stickyImg} />
                            <div>
                                <strong>{product.title}</strong>
                                <p>${product.price}</p>
                            </div>
                        </div>
                        <a href={product.link} target="_blank" className="btn btn-primary">
                            Check Price on Amazon
                        </a>
                    </div>
                </div>
            </div>

            <div className="container">
                {/* Breadcrumbs */}
                <nav className={styles.breadcrumbs}>
                    <a href="/">Home</a> / <a href="/categories">Categories</a> / <span>{product.title}</span>
                </nav>

                <div className={styles.mainGrid}>
                    {/* Image Gallery Placeholder */}
                    <div className={styles.gallery}>
                        <div className={styles.mainImage}>
                            <Image src={product.image} alt={product.title} width={600} height={600} />
                        </div>
                    </div>

                    {/* Product Info */}
                    <div className={styles.info}>
                        <span className={styles.category}>{product.category}</span>
                        <h1 className={styles.title}>{product.title}</h1>

                        <div className={styles.rating}>
                            {"★".repeat(Math.floor(product.rating))} <span>({product.rating} / 5 based on 128 reviews)</span>
                        </div>

                        <div className={styles.priceSection}>
                            <div className={styles.priceWrapper}>
                                <span className={styles.price}>${product.price}</span>
                                {product.oldPrice && <span className={styles.oldPrice}>${product.oldPrice}</span>}
                            </div>
                            <a href={product.link} target="_blank" className="btn btn-primary">
                                View Price on Amazon
                            </a>
                        </div>

                        <div className={styles.summary}>
                            <h3>Our Review Summary</h3>
                            <p>{product.reviewSummary}</p>
                        </div>
                    </div>
                </div>

                {/* Detailed Review Section */}
                <div className={styles.details}>
                    <div className={styles.prosCons}>
                        <div className={styles.pros}>
                            <h3>What we Liked</h3>
                            <ul>
                                {product.pros.map((pro, i) => <li key={i}>✅ {pro}</li>)}
                            </ul>
                        </div>
                        <div className={styles.cons}>
                            <h3>What could be better</h3>
                            <ul>
                                {product.cons.map((con, i) => <li key={i}>❌ {con}</li>)}
                            </ul>
                        </div>
                    </div>

                    <div className={styles.features}>
                        <h3>Technical <span className="text-gradient">Specifications</span></h3>
                        <div className={styles.featureGrid}>
                            {product.features.map((feature, i) => (
                                <div key={i} className={styles.featureItem}>
                                    <span>{feature}</span>
                                </div>
                            ))}
                        </div>
                    </div>

                    <div className={styles.faq}>
                        <h3>Frequently Asked <span className="text-gradient">Questions</span></h3>
                        <div className={styles.faqList}>
                            {product.faqs.map((faq, i) => (
                                <div key={i} className={styles.faqItem}>
                                    <h4>{faq.q}</h4>
                                    <p>{faq.a}</p>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
